import React from 'react';

import { primaryColor } from '../common/constants';

const Card = props => (
	<div className="card">
		Card
		<Style />
	</div>
);

const Style = () => (
	<style jsx>{`
		.card {
			flex: 1;
			color: white;
			background-color: red;
			border: 1px solid white;
		}
	`}</style>
);

export default Card;



